.App {
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  /*min-height: 100vh;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 15px;
  padding-right: 15px;
  color: white;
}
ul{
  list-style: none;
  text-align: start;
  padding: 0;
}
li{
  margin-top: 5px;
  margin-bottom: 5px;
}
p{
  margin-top: 0;
  margin-bottom: 0;
}
.App-link {
  color: #61dafb;
}
h1{
  margin-top: 10px;
  margin-bottom: 3px;
}
h2{
  margin-top: 3px;
  margin-bottom: 3px;

}
main{
  height: 100%;
  color: white;
  padding: 15px;
  background-color: #202030;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
